<template>
  <div class="col">
    <!--     행동 리스트     -->
    <div class="row text-center mb-1">
      <h4 class="col-11 text-start">행동 리스트</h4>
      <div class="col-1">
        <button class="btn btn-primary" disabled>
          <font-awesome-icon icon="floppy-disk"/>
        </button>
      </div>
    </div>
    <div class="row text-center">
      <div class="col-2 m-auto">name</div>
      <div class="col-1 m-auto">run</div>
      <div class="col-1 m-auto">loop</div>
      <div class="col-1 m-auto">stop</div>
      <div class="col-1 m-auto">edit</div>
      <div class="col-2 m-auto">proxyType</div>
      <div class="col-1 m-auto">isGlobal</div>
      <div class="col-2 m-auto">waitTime</div>
      <div class="col-1">
        <button class="btn btn-primary" disabled>
          <font-awesome-icon icon="plus"/>
        </button>
      </div>
    </div>

    <hr/>

    <draggable :list="actions" item-key="id">
      <template #item="{ element : action }">
        <div class="row text-center mb-3">

          <!--        name        -->
          <div class="col-2">
            <input type="text" class="form-control" v-model="action.name">
          </div>

          <!--        run        -->
          <div class="dropdown col-1">
            <button class="btn btn-light" v-on:click="run_action(action)">
              <font-awesome-icon icon="play"/>
            </button>
          </div>

          <!--        loop        -->
          <div class="dropdown col-1">
            <button class="btn btn-light" v-on:click="loop_action(action)">
              <font-awesome-icon icon="repeat"/>
            </button>
          </div>

          <!--        stop        -->
          <div class="col-1">
              <button @click="stop_action()" class="btn btn-light">
                <font-awesome-icon icon="stop"/>
              </button>
          </div>

          <!--        edit        -->
          <div class="col-1">
              <button class="btn btn-light" disabled>
                <font-awesome-icon icon="pen-to-square"/>
              </button>
          </div>

          <!--        proxy type        -->
          <div class="col-2">
            <select class="form-select" v-model="action.proxy_type">
                  <option v-for="proxyTypeEnum in this.proxyEnums['proxyType']" v-bind:key="proxyTypeEnum.key" :value="proxyTypeEnum.value">
                    {{proxyTypeEnum.key}}
                  </option>
            </select>
          </div>

          <!--        is global        -->
          <div class="col-1">
            <input class="form-check-input btn-common-size checkbox-secondary" type="checkbox" v-model="action.is_global">
          </div>

          <!--        wait time        -->
          <div class="col-2">
            <delta-time-input v-model="action.wait_time"/>
          </div>

          <!--       delete        -->
          <div class="col-1">
            <button class="btn btn-light" disabled>
              <font-awesome-icon icon="minus"/>
            </button>
          </div>
        </div>
      </template>
    </draggable>

    <hr class="m-0"/>

  </div>

</template>

<script>
import {defineComponent} from "vue";
import axios from "axios";
import draggable from "vuedraggable";
import deltaTimeInput from "@/components/props/input/DeltaTimeInput.vue";
import Action from "@/model/Action";

export default defineComponent({
  name: "AutomationPhoneActionList",

  components: {
    draggable, deltaTimeInput
  },

  data() {
    return {
      udid: this.$route.query.udid,
      actions: [],
      proxyEnums: {},
      runMachineActionParameters: {
        actionSeq: null
      },
      stopPhoneActionParameters: {
      }
    }
  },

  async created() {
    this.get_machine_actions()
    this.get_proxy_enums()
  },

  methods: {

    /**
     * phone action
     */
    get_machine_actions() {
      axios.get('http://127.0.0.1:8082/api/automation/machine/actions')
           .then(res => {
             this.actions = res.data.map(action => new Action(action))
           })
           .catch(err => {console.log(err)})
    },

    run_action(action) {
      this.runMachineActionParameters.actionSeq = action.action_seq
      axios.get('http://127.0.0.1:8082/api/automation/machine/action/appium/run', {params : this.runMachineActionParameters})
          .then(res => {console.log(res)})
          .catch(error => (console.log(error)))
    },

    loop_action(action) {
      this.runMachineActionParameters.actionSeq = action.action_seq
      axios.get('http://127.0.0.1:8082/api/automation/machine/action/appium/loop', {params : this.runMachineActionParameters})
          .then(res => {console.log(res)})
          .catch(error => (console.log(error)))
    },

    stop_action() {
      axios.get('http://127.0.0.1:8082/api/automation/machine/action/appium/stop')
          .then(res => {console.log(res)})
          .catch(error => (console.log(error)))
    },

    /**
     * proxy
     */
    get_proxy_enums() {
      axios.get('http://127.0.0.1:8082/api/utils/enum/proxy')
          .then(res => {this.proxyEnums = res.data})
          .catch(error => (console.log(error)))
    },
  }
})
</script>

<style scoped>
.btn-common-size {
  width: 2rem;
  height: 2rem;
}
.checkbox-secondary:checked {
  background-color: dimgray;
}
</style>